<script>
import { mapState } from 'vuex'
import NProgress from 'nprogress'
export default {
  name: 'BookShow',
  created() {
    NProgress.start()
    this.$store.dispatch('book/fetchBook', this.$route.params.id)
  },
  mounted() {
    NProgress.done()
  },
  computed: {
    ...mapState(['book'])
  }
}
</script>

